.page {
    margin: 0 auto;
    background-color: #fff;
    min-width: 320px;
    width: 100%;
}

/*
div, p, img, a, ul, li, header, nav, footer, h1, h2, h3, h4, section, button {
    border: solid red 1px;
}
*/
.list {
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.list_style_dot {
    list-style-type: circle;
    padding-left: 20px;
}
.hyperlink {
    text-decoration: none;
    margin: 0;
    padding: 0;
    opacity: 1;
    transition: opacity .3s ease-in;
    color: #000;
}

.hyperlink:hover {
    opacity: .6;
}
.hyperlink_color_white {
    color: #fff;
}
.header {
    min-height: 100px;
    width: 100%;
    background: #520f21;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    opacity: .9;
    z-index: 3;
}

@media screen and (max-width: 900px) {
    .header {
        min-height: 75px;
    }
} 

@media screen and (max-width: 730px) {
    .header {
        flex-direction: column;
        position: relative;
    }
}
.header__logo {
    width: 225px;
    height: 35px;
    margin: 35px 0 0 25px;
    cursor: pointer;
}


@media screen and (max-width: 1050px) {
    .header__logo {
        width: 200px;
        height: 31px;
    }
}

@media screen and (max-width: 900px) {
    .header__logo {
        width: 175px;
        height: 28px;
        margin: 25px 0 0 18px;
    }
}

@media screen and (max-width: 360px) {
    .header__logo {
        width: 150px;
        height: 24px;
        margin: 18px 0 0 18px;
    }
}
.header__link {
    color: #fff;
    text-decoration: none;
    transition: color .2s ease-in;
}

.header__link:hover {
    color: #daa520;
}
.header__menu {
    display: flex;
    width: 750px;
    justify-content: space-between;
    margin: 40px 40px 0 0;
    padding: 0;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 18px;
    list-style-type: none;
}

@media screen and (max-width: 1050px) {
    .header__menu {
        width: 600px;
        font-size: 16px;
    }
}

@media screen and (max-width: 900px) {
    .header__menu {
        width: 500px;
        font-size: 14px;
        margin: 32px 32px 0 0;
    }
} 


@media screen and (max-width: 730px) {
    .header__menu {
        flex-direction: column;
        margin: 18px 0 10px 18px;
        width: 100%;
        min-height: 100px;
    }
}
.header__nav {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}
.header__contacts {
    display: none;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 12px;
}

@media screen and (max-width: 730px) {
    .header__contacts {
        display: flex;
        flex-direction: column;
        margin: 18px 18px 0 0;
        height: 35px;
        justify-content: space-between;
        align-items: flex-end;
    }
}

@media screen and (max-width: 350px) {
    .header__contacts {
        display: none;
    }
}
.header__logo-contacts {
    display: flex;
    justify-content: space-between;
}
.gasification {
    height: calc(100vh - 100px);
    margin: 100px 0 0 0;
    display: flex;
    flex-direction: column;
    background-image: url(/techservice31-react/static/media/background-main.09205f1e.jpg);
    background-size: cover;
    background-position: top;
    background-repeat: no-repeat;
    position: relative;
}

.gasification::after {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #2A2C2F;
    opacity: .4;
    z-index: 1;
}

@media screen and (max-width: 900px) {
    .gasification {
        margin: 75px 0 0 0;
        height: calc(100vh - 75px);
    }
} 

@media screen and (max-width: 730px) {
    .gasification {
        margin: 0;
        width: 100%;
        height: unset;
    }
}
.gasification__title {
    margin: 70px 0 50px 100px;
    max-width: 800px;
    padding: 0;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 52px;
    font-weight: 400;
    color: #fff;
    z-index: 2;
}

@media screen and (max-width: 900px) {
    .gasification__title {
        font-size: 46px;
        margin: 100px 0 10px 50px;
    }
} 

@media screen and (max-width: 730px) {
    .gasification__title {
        font-size: 36px;
        margin: 70px 0 10px 50px;
        max-width: 530px;
    }
}

@media screen and (max-width: 530px) {
    .gasification__title {
        font-size: 24px;
        margin: 50px 0 10px 30px;
        max-width: 350px;
    }
} 
.gasification__text {
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 32px;
    line-height: 1.4;
    margin: 25px 0 0 100px;
    max-width: 500px;
    color: #fff;
    z-index: 2;
}

@media screen and (max-width: 900px) {
    .gasification__text {
        font-size: 24px;
        margin: 50px 0 0 50px;
    }
} 

@media screen and (max-width: 730px) {
    .gasification__text {
        font-size: 20px;
        margin: 40px 0 0 50px;
        max-width: 300px;
    }
}

@media screen and (max-width: 530px) {
    .gasification__text {
        font-size: 16px;
        margin: 20px 0 0 30px;
        max-width: 250px;
    }
}
.gasification__button {
    width: 160px;
    height: 60px;
    background: #810B44;
    margin: 100px 0 0 100px;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 60px;
    color: #fff;
    border: none;
    cursor: pointer;
    position: relative;
    text-align: center;
    text-decoration: none;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
    z-index: 2;
}

.gasification__button:before {
	content:'';
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 0;
	background: rgba(255,255,255,0.2);
	transition: all .5s ease;
}

.gasification__button:hover:before {
	height: 60px;
}

@media screen and (max-width: 900px) {
    .gasification__button {
        margin: 150px 0 50px 50px;
    }
} 

@media screen and (max-width: 730px) {
    .gasification__button {
        margin: 20px 0 50px 50px;
    }
} 

@media screen and (max-width: 530px) {
    .gasification__button {
        width: 130px;
        height: 40px;
        background: #810B44;
        margin: 20px 0 50px 30px;
        font-family: 'Rubik', Arial, Helvetica, sans-serif;
        font-size: 12px;
        font-weight: 400;
        line-height: 40px;
        color: #fff;
        border: none;
        cursor: pointer;
        position: relative;
        text-align: center;
        text-decoration: none;
        box-shadow: 0 0 10px rgba(0, 0, 0, .5);
        z-index: 2;
    }

    .gasification__button:hover:before {
        height: 40px;
    }
} 
