    bottom: 20px;
    right: 0;
    left: 0;
    margin: 0 auto;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 14px;
    cursor: pointer;
    opacity: 1;
    transition: opacity .5s ease-in;
}

.addition__button:hover {
    opacity: .5;
}
.footer {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    background-color: #520f21;
    margin: 0;
    padding: 0;
}
.footer__title {
    color: #fff;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.4;
    margin: 100px auto 50px;
    padding: 0;
    text-align: center;
}

@media screen and (max-width: 730px) {
    .footer__title {
        margin: 50px auto;
        font-size: 26px;
    }
}

@media screen and (max-width: 530px) {
    .footer__title {
        font-size: 24px;
    }
}
.footer__text {
    color: #fff;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.4;
    margin: 0 auto 10px;
    padding: 0;
}
.footer__map {
    height: 200px;
    width: calc(100% - 200px);
    margin: 0 auto;
}
.footer__contacts-container {
    width: calc(100% - 200px);
    display: flex;
    justify-content: space-between;
    margin: 50px auto;
}

@media screen and (max-width: 730px) {
    .footer__contacts-container {
        flex-direction: column;
    }
}

@media screen and (max-width: 530px) {
    .footer__contacts-container {
        width: calc(100% - 60px);
    }
}
.footer__address {
    text-align: right;
}

@media screen and (max-width: 730px) {
    .footer__address {
        text-align: center;
    }
}
.footer__contacts {
    text-align: left;
}

@media screen and (max-width: 730px) {
    .footer__contacts {
        text-align: center;
    }
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
    display: flex;
    visibility: hidden;
    opacity: 0;
    transition: visibility .5s, opacity .5s linear;
    justify-content: center;
    align-items: center;
    z-index: 4;
}
.popup_opened {
    display: flex;
    visibility: visible;
    opacity: 1;
}
.popup__content {
    width: calc(100% - 200px);
    height: calc(100% - 200px);
    background-color: #ffffff;
    border-radius: 10px;
    box-sizing: border-box;
    position: relative;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.15);
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 767px) {
    .popup__content {
        min-width: 282px;
        min-height: 322px;
    }
}
.popup__close {
    background: url(data:image/png;base64,);
    background-size: contain;
    width: 50px;
    height: 50px;
    border: none;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    transition: opacity .5s ease-in;
}

.popup__close:hover {
    opacity: .6;
}
.popup__title {
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin: 60px auto 20px;
    text-align: center;
}

@media screen and (max-width: 767px) {
    .popup__title {
        font-size: 18px;
    }
}

.popup__text {
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
    font-size: 20px;
    font-weight: 400;
    margin: 0 40px 40px ;
    line-height: 1.4;
    text-align: justify;
}

@media screen and (max-width: 767px) {
    .popup__text {
        font-size: 16px;
    }
}
.popup__description {
    display: flex;
    margin: 0 0 10px 0;
    text-indent: 20px;
}
.popup__list {
    margin: 0 0 10px 0;
}
.popup__image {
    width: 80%;
    margin: 0 auto 50px;
    object-fit: cover;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
}
.popup__form {
    width: 500px;
    height: 300px;
    font-family: 'Rubik', Arial, Helvetica, sans-serif;
}
.popup__form-container {
    display: flex;
    flex-direction: column;
}
.popup__input {
    height: 34px;
    width: 100%;
    margin: 5px auto 0;
    border-left: none;
    border-right: none;
    border-top: none;
    border-bottom: solid 1px #E8E8E8;
    font-size: 20px;
    font-weight: 400;
    outline: none;
}
.popup__label {
    position: relative;
    margin: 30px 50px 0;
    font-size: 14px;
    color: #A0A0A0;
}
.popup__input-error {
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    font-size: 12px;
    font-weight: 400;
    color: #EE3465;
    visibility: visible;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.popup__submit {
    width: 400px;
    height: 45px;
    border-radius: 3px;
    color: #fff;
    background-color: #520f21;
    margin: 40px auto 0;
    border: none;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
}

.popup__submit_disabled {
    background-color: #F8F8F8;
    color: #C2C2C2;
    cursor: default;
}
