.popup {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, .5);
    display: flex;
    visibility: hidden;
    opacity: 0;
    transition: visibility .5s, opacity .5s linear;
    justify-content: center;
    align-items: center;
    z-index: 4;
}